.tb_m_container {
       padding: 15px;
       border-bottom: 1px solid rgba(184, 184, 184, .40);
       margin-bottom: 15px;
}

.tb_m_wrapper {
       width: 100%;
}

.tb_m_title{
       font-size: var(--tb-re-title-size);
       font-weight: 400;
       margin-bottom: 5px;
       text-align: center;
        color:var(--tb-re-title-color);
        font-family: var(--tb-re-title-font);
        
}

.tb_m_avg,
.tb_m_wrapper {
       display: flex;
       flex-wrap: wrap;
       align-items: center;
}

.tb_m_avg{
       margin-bottom: 10px;
}

.tb_m_wrapper {
       justify-content: space-between;
}

.tb_m_rating_wrap{
       display: flex;
       flex-direction: column;
}

.tb_m_total{    
       font-family: var(--tb-font);
       color: var(--tb-color);
       font-size: var(--tb-font-size);
       line-height: var(--tb-line-height);
}

.tb_m_rview_star {
       margin-right: 10px;
}
.tb_m_rview_star .tb_avg_rating_ico__,.tb_m_rview_star .tb_avg_rating_ico_fill__,.tb_c_avg_list .tb_avg_rating_ico__,.tb_avg_rating_ico__,.tb_rating_ico__ {
       color: var(--tb-review-start-color)!important;
       }

.tb_m_banner_btn {
        background-color: var(--tb-btn-review-bg-color);
       color: var(--tb-btn-review-color);
       padding: 10px 20px;
        font-size: var(--tb-btn-review-size);
       transition: opacity .3s ease-in-out;
       cursor: pointer;
           font-family: var(--tb-btn-review-font);
}

.tb_m_banner_btn:hover {
       opacity: .8;
}

.tb_m_banner_btn_wrap {
       position: relative;
       margin: 8px 0;
}

@media screen and (max-width: 767px) {
       .tb_m_banner_btn {
              padding: 9px 14px;
              font-size: var(--tb-btn-review-size);
       }

       .tb_m_rview_star {
              margin-right: 5px;
       }

       .tb_m_rview_star .tb_avg_rating_ico__ {
              font-size: 14px;
              margin: 1px;
       }

       .tb_m_review_total_post,
       .tb_m_total {
              font-size: 12px;
       }
}

@media screen and (max-width: 480px) {
       .tb_m_banner_wrap {
              padding: 10px 15px;
       }
}